<script>
  import VButton from '../../molecules/buttons/VButton'
  import ButtonWave from '../../molecules/buttons/ButtonWave'
  import VCheckbox from '../../molecules/inputs/VCheckbox'

  export default {
    components: {
      VButton,
      ButtonWave,
      VCheckbox
    },
    props: {
      table: Object,
    },
    data() {
      return {
        tableMobile: [
          {
            id: 1,
            row: {
              login: 'первый',
              password: '1212e1',
              status: false,
            },
            data: new Map([
              ['Логин', 'первый'],
              ['Пароль', '1212e1'],
              ['Пол', 'Женский'],
              ['Статус', 'Заполняется']
            ])
          },
          {
            id: 2,
            row: {
              login: 'Второй',
              password: '1212e1',
              status: false,
            },
            data: new Map([
              ['Логин', 'Второй'],
              ['Пароль', '1212e1'],
              ['Пол', 'Женский'],
              ['Статус', 'Заполняется']
            ])
          },
        ],
     /* {
        login: 'фцвфцвфцв',
          password: '1212e1',
        sex: 'Женский',
        fill: 'Заполняется'
      }*/
				openId: null,
        data: null,
        headers: null,
        deleteElems: [],
        selectedAll: false,
        clicked: false,
        isShow: false,
      }
    },
    created() {
      this.data = this.table.data;
      this.headers = this.table.headers;
    },
    computed: {
      btnText() {
        let el_length = this.deleteElems.length;
        let table_length = this.table.data.length;
        if (el_length > table_length / 2 || el_length === table_length) {
          this.selectedAll = true;
          return 'Снять выделение'
        } else if (!el_length || (el_length <= table_length / 2)) {
          this.selectedAll = false;
          return 'Выделить все'
        }
      },
      selected() {
        let result = [];
        if (this.selectedAll) {
          this.table.data.forEach(el => {
            result.push(el.id);
          });
          return result;
        } else return result.splice(0, result.length);
      },
    },
    methods: {
      openDrop(index) {
        this.openId = index;
        this.isShow = !this.isShow
      },
      checkAll() {
        this.selectedAll = !this.selectedAll;
        this.deleteElems = this.selected;
      },
      removeTable() {
        let idx, length = this.data.length;
        this.deleteElems.forEach(id => {
          for (idx = 0; idx < length; idx++) {
            if (this.table.data[idx].id === id) {
              this.table.data.splice(idx, 1);
              break;
            }
          }
        });
        this.selectedAll = false;
      },
	    deleteItem(index) {
        this.tableMobile.forEach((item, i) => {
          if (this.openId === i) this.tableMobile.splice(i, 1);
        })
	    }
    }
  }
</script>
