<!-- TODO: Сделать анимацию для выпадающего списка -->
<template>
	<div>
		<label :for="id"
		       class="select-label"
		>
			{{ label }}
		</label>
		<div class="custom-select"
		     :tabindex=tabindex
		     @blur="open = false"
		>
			<div class="selected" @click="open = !open">
				{{selected}}
				<span class="toggle-select"
				      :class="{'toggle-select--rotate': open}"
				>
				<svg width="1em" height="1em">
					<use xlink:href="#icon-arrow-select">
						<svg id="icon-arrow-select" viewBox="0 0 20 20">
                <path d="M3.225 5.40832L1.75 6.88332L10 15.1333L18.25 6.88333L16.775 5.40833L10 12.1833L3.225 5.40832Z"
                      fill="currentColor"></path>
            </svg>
					</use>
				</svg>
			</span>
			</div>
				<div class="options"
				     v-show="open">
					<div class="options__item"
					     v-if="options.length"
					     v-for="(option, index) of options"
					     :key="index"
					     @click="selection(option, $event)"
					>
						{{option}}
					</div>
				</div>
		</div>
	</div>

</template>

<script>
	import VTextArea from './VTextArea'

  export default {
    component: {
      VTextArea
    },
    props: {
      options: {
        type: Array,
        required: false
      },
      tabindex: {
        type: Number,
        required: false,
        default: 0
      },
      label: String,
      labelClass: {
        type: String,
        required: false
      },
      id: String,
      search: {
        type: Boolean,
        required: false
      }
    },
    data() {
      return {
        open: '',
        searchModel: '',
        selected: this.options.length > 0 ? this.options[0] : null
      }
    },
    mounted() {
      this.$emit('input', this.selected);
    },
    computed: {},
    methods: {
      selection(option,event) {
        this.selected = option;
        if (event.target !== this.$refs.searchSelect) {
          this.open = !this.open;
          this.$emit('selection', option);
        } else return false;
      }
    }
  }
</script>

<style lang="scss">
	.custom-select {
		position: relative;
		white-space: nowrap;
		text-overflow: ellipsis;
		border: 1px solid transparent;
		border-radius: 4px;
		font-size: 16px;
		color: #fff;
		background: #353539;
		cursor: pointer;
		transition: all 0.3s ease;
		&:hover {
			border-color: #ffc107;
		}
		&:focus {
			border-color: #ffc107;
			outline: none;
			background: #fff;
		}
	}

	.selected {
		border-radius: 4px;
		background: #353539;
		padding: 15px 35px 15px 15px;
	}

	.toggle-select {
		svg {
			position: absolute;
			top: 16px;
			right: 15px;
			margin: auto 0;
			font-size: 18px;
			color: #fff;
			pointer-events: none;
			transition: .3s ease-in;
		}
		&--rotate svg {
			transform: rotateX(180deg);
		}
	}

	.options {
		position: absolute;
		top: 50px;
		left: 0;
		z-index: 1000;
		width: 100%;
		padding: 0;
		background: #353539;
		transition: all 0.5s ease;
		box-shadow: 0 3px 7px rgba(46, 80, 165, 0.29);
		max-height: 260px;
		overflow-y: auto;
		border-radius: 4px;
		&__item {
			padding: 10px 15px;
			&:hover {
				background: rgba(255, 193, 7, 0.2);
			}
		}
	}
	
	@media screen and (min-width: 812px) {
		.custom-select,
		.options {
			background: #fff;
			color: #000;
		}
		.selected {
			background: #f8f8f8;
			color: #000;
		}
		.custom-select {
			border-color: #dcdcdc;
		}
		.toggle-select svg {
			color: #324354 ;
		}
	}
</style>