import Vue from "vue";
import Vuex from "vuex";
// import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    modals: {},
    modal: {
      isOpen: false,
      id: null,
      name: null
    },
    accounts: [],
    messageTemplates: [
      {
        id: 1,
        template: [
          {
            id: 1,
            messages: [
              {
                id: 1,
                condition: 'фцвфц',
                text: 'фцвфцв',
              }
            ]
          },
          {
            id: 2,
            messages: [
              {
                id: 1,
                condition: 'фцвф',
                text: 'фцвфцв',
              }
            ]
          },
          {
            id: 3,
            messages: [
              {
                id: 1,
                condition: '',
                text: '',
              }
            ]
          },
          {
            id: 4,
            messages: [
              {
                id: 1,
                condition: '',
                text: '',
              }
            ]
          },
          {
            id: 5,
            messages: [
              {
                id: 1,
                condition: '',
                text: '',
              }
            ]
          },
        ]
      },
      {
        id: 2,
        template: [
          {
            id: 1,
            messages: [
              {
                id: 1,
                condition: 'male: template 1',
                text: '',
              }
            ]
          },
          {
            id: 2,
            messages: [
              {
                id: 1,
                condition: '',
                text: '',
              }
            ]
          },
          {
            id: 3,
            messages: [
              {
                id: 1,
                condition: '',
                text: '',
              }
            ]
          },
          {
            id: 4,
            messages: [
              {
                id: 1,
                condition: '',
                text: '',
              }
            ]
          },
          {
            id: 5,
            messages: [
              {
                id: 1,
                condition: '',
                text: '',
              }
            ]
          }
        ]
      },
    ]
  },
  mutations: {
    ADD_ACCOUNT(state, obj) {
      if (!state.accounts.length) obj.id = 1;
      else obj.id = state.accounts.length + 1;
      state.accounts.push(obj);
    },
    ADD_TEMPLATE_FOR_MESSAGE(state, template) {
      let length = template.messages.length;
      template.messages.push({
        id: length ? (template.messages[length - 1].id + 1) : 1,
        condition: '',
        text: ''
      });
    },
    REMOVE_TEMPLATE_FOR_MESSAGE(state, {selectedTab, selectedTemplate, index}) {
      state
        .messageTemplates[selectedTab]
        .template[selectedTemplate]
        .messages
        .splice(index, 1)
    },
    SHOW_MODAL(state, modalData) {
      if (modalData instanceof Object) {
        state.modal.name = modalData.name;
        state.modal.id = modalData.id
      } else {
        state.modal.name = modalData;
      }
      state.modal.isOpen = true;
    },
    CLOSE_MODAL(state) {
      state.modal.isOpen = false;
      // state.modal.type = null;
    }
  },
  actions: {
    removeAccount({commit}, obj) {
      commit('REMOVE_ACCOUNT', obj);
    },
    async addTemplateMessage({commit, state}, {selectedTab, selectedTemplate}) {
      let container = document.querySelector('.tab-message__content');
      let scrollable = document.querySelector('.tab-container-scrollable');
      let tab = state.messageTemplates[selectedTab],
        template = tab.template[selectedTemplate];

      if (template.messages.length < 10) {
        await commit('ADD_TEMPLATE_FOR_MESSAGE', template);
        if (template.messages.length >= 2) container.scrollTop = scrollable.scrollHeight;
      } else {
        alert('Вы достигли лимита для данного сообщения(10)')
      }
    },
    removeTemplateMessage({commit, state}, obj) {
      commit('REMOVE_TEMPLATE_FOR_MESSAGE', obj);
    },
    showModal({commit}, modalData) {
      document.body.classList.add('no-scroll');
      commit('SHOW_MODAL', modalData)
    },
    openInstructionsModal({commit}, id) {
      if (!id) return;
      commit('SHOW_MODAL', {
        name: 'ModalInstruction',
        id: id
      })
    },
    closeModal({commit}) {
      document.body.classList.remove('no-scroll');
      commit('CLOSE_MODAL');
    }
  },
  getters: {
    modalIsOpen(state) {
      return state.modal.isOpen;
    },
    modalComponent(state) {
      if (state.modal.name) return () => import(`@/components/modal/${state.modal.name}`);
      else return false;
    },
    modalId(state) {
      return state.modal.id
    }
  },
  // plugins: [createPersistedState()]
});
