<!--TODO: в блоке "Добавить аккаунты" изменять подпись к чекбоксу взависимости от количества (обращаться к длине массива в store) -->
<template>
	<form class="autopilot-form" action="#!" method="GET">
		<h1 class="content-ttl">
			<a href="#!" class="content-ttl__link">
				<svg width="1em" height="1em" class="icon icon-arrow-back content-ttl__icon">
					<use xlink:href="#icon-arrow-back">
						<svg id="icon-arrow-back" viewBox="0 0 24 24">
							<path d="M20 11H7.83L13.42 5.41L12 4L4 12L12 20L13.41 18.59L7.83 13H20V11Z" fill="currentColor"></path>
						</svg>
					</use>
				</svg>
				<span>Автопилот</span>
			</a>
		</h1>
		<!-- ACCOUNT BLOCK-->
		<v-block block-name="account">
			<v-text-field
				class="input-wrap"
				type="text"
				label="ID основного аккаунта"
				label-class="id-label"
				id="for-account"
				placeholder="например: 123456789"
				v-model="model.primaryAccountId"
			/>
			<v-instructions @click="openInstructionsModal('vk')" text="Где брать ID основного аккаунта VK?"/>
			<v-button
				:disabled="primaryAccountId.length <= 5"
				@click="saveSelectVal(primaryAccountId)"
			></v-button>
		</v-block>
		<!-- GREETING BLOCK -->
		<v-block block-name="greeting" block-title="Приветствие">
			<v-tabs :data="greetingTabData" @blur="saveGreeting"/>
			<v-instructions @click="openInstructionsModal('greeting')"/>
		</v-block>
		<!-- AUTO DIALOGS BLOCK -->
		<v-block block-name="dialogs" block-title="Автодиалоги">
			<v-checkbox
				id="for-dialogs"
				label="Использовать автодиалоги"
				value="Джек"
				v-model="model.useAutoDialogs"
			/>
			<transition name="slide-appearance" mode="out-in">
				<div v-if="model.useAutoDialogs" class="block-dropdown">
					<v-button
						class="mt-20"
						btn-style="lg"
						@click="showModal('ModalAutoDialogs')"
					>настройка диалогов
					</v-button>
					<!--<modal-target-settings v-if="modalTargetIsOpen" @close="closeModal('modalTargetIsOpen')"/>-->
				</div>
			</transition>
			<v-instructions @click="openInstructionsModal('dialogs')"></v-instructions>
		</v-block>
		<!-- TARGET SETTINGS BLOCK -->
		<v-block block-name="target"
		         block-title="Настройка целевой аудитории"
		>
			<div class="v-box">
				<v-radio
					id="for-target1"
					label="Использовать свою ЦА"
					key="own-ta"
					value="one"
					v-model="model.whatToUseInTargetSettings"
				/>
				<transition name="smooth-fast" mode="out-in">
					<div v-if="useYourAuditory" class="block-dropdown">
						<div class="block-dropdown__title">Количество отправляемых заявок в друзья</div>
						<div class="input-group">
							<v-text-field
								class="input-wrap"
								type="text"
								placeholder="от"
								v-model="model.targetAuditoryOwnFrom"
							/>
							<v-text-field
								class="input-wrap"
								type="text"
								placeholder="до"
								v-model="model.targetAuditoryOwnTo"
							/>
						</div>
						<v-button
							@click="saveSelectVal([model.targetAuditoryOwnFrom, model.targetAuditoryOwnTo])"
							:disabled="!fromToValidation"
						></v-button>
						<v-button
							class="mt-20"
							btn-style="lg"
							@click="showModal('ModalTargetSettings')"
						>настройка ца
						</v-button>
					</div>
				</transition>
			</div>
			<div class="v-box">
				<v-radio
					id="for-target2"
					label="Использовать автоматически собранную ЦА"
					key="auto-ta"
					value="two"
					v-model="model.whatToUseInTargetSettings"
				/>
				<transition name="smooth-fast" mode="out-in">
					<div v-if="useCollectedAuditory" class="block-dropdown">
						<div class="block-dropdown__title">Количество отправляемых заявок в друзья</div>
						<v-control-select search :options="selectOptionsAuto" @selection="selection"></v-control-select>
						<v-button
							class="mt-20"
							btn-style="reg"
							@click="saveSelectVal(selected)"
							:disabled="!selected.length"/>
					</div>
				</transition>
			</div>
			<div class="v-box">
				<v-radio
					id="for-target3"
					label="Рассылка по рекомендованным друзьям"
					key="friend-ta"
					value="three"
					v-model="model.whatToUseInTargetSettings"
				/>
				<transition name="smooth-fast" mode="out-in">
					<div v-if="useRecommendMailing" class="block-dropdown">
						<div class="block-dropdown__title">Количество отправляемых заявок в друзья</div>
						<v-control-select :options="selectOptionsFriend" @selection="selection"></v-control-select>
						<v-button
							class="mt-20"
							:disabled="!selected.length"
							btn-style="reg"
							@click="saveSelectVal(selected)"
						/>
					</div>
				</transition>
			</div>
			<v-instructions @click="openInstructionsModal('target')"/>
		</v-block>
		<!-- AUTO-POSTING BLOCK-->
		<v-block block-name="auto-post"
		         block-title="Репостинг и автопостинг"
		>
			<v-checkbox
				id="for-reposting"
				label="Использовать репостинг и автопостинг"
				key="key-reposting"
				v-model="model.useAutoReposting"
			/>
			<transition name="smooth-fast">
				<div v-if="model.useAutoReposting">
					<v-info info="Внимание! В репостинге учавствуют только женские аккаунты, контент только женский!"/>
				</div>
			</transition>
			<v-instructions @click="openInstructionsModal('reposting')"/>
		</v-block>
		<!-- ADDING ACCOUNT BLOCK -->
		<v-block block-name="add-account"
		         :block-title="`Добавить аккаунты (${counter}/${counter})`"
		>
			<v-switcher></v-switcher>
			<v-checkbox
				id="for-fill-accounts"
				label="Заполнить аккаунт(ы)"
				key="fill-accounts"
				v-model="model.fillInAccounts"
			/>
			<transition name="smooth-fast">
				<v-info v-if="model.fillInAccounts" info="Внимание! в заполнении учавствуют только женские аккаунты, контент только
					женский!"/>
			</transition>
			<v-button class="mt-20" :disabled="!model.fillInAccounts">Добавить</v-button>
			<v-instructions @click="openInstructionsModal('accounts')"/>
		</v-block>
		<transition name="slide-appearance" mode="out-in">
			<table-account/>
		</transition>
	</form>
</template>
<script>
  import VTextField from '../molecules/inputs/VTextField'
  import VTextArea from '../molecules/inputs/VTextArea'
  import VButton from '../molecules/buttons/VButton'
  import VBlock from '../components/VBlock'
  import VTabs from '../molecules/tabs/VTabs'
  import VInfo from '../components/info/VInfo'
  import VInstructions from '../components/info/VInstructions'
  import VCheckbox from '../molecules/inputs/VCheckbox'
  import VRadio from '../molecules/inputs/VRadio'
  import VControl from '../molecules/inputs/VControl'
  import TableAccount from '../components/table/TableAccount'
  import VControlSelect from '../molecules/inputs/VControlSelect'
  import VSwitcher from '../molecules/tabs/VSwitcher'
  import ModalInstruction from '../components/modal/ModalInstruction'
  import {mapActions} from 'vuex'

  export default {
    components: {
      ModalInstruction,
      VTextField,
      VTextArea,
      VButton,
      VBlock,
      VTabs,
      VInfo,
      VInstructions,
      VControl,
      VControlSelect,
      VCheckbox,
      VRadio,
      VSwitcher,
      TableAccount,
    },
    props: {
      empty: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        modalTargetIsOpen: false,
        modalDialogsSettingsIsOpen: false,
        isOpen: true,
        dataForTable: {
          sex: 'female',
          fillAllAccounts: false,
          accountData: [],
        },
        dataForBackend: {
          primaryAccountId: '',
          reposting: false,
          targetAuditoryOwnFrom: '',
          targetAuditoryOwnTo: '',
          targetAuditoryAutoCollect: '',
          targetAuditoryRecommendFriends: '',
          greetingMale: '',
          greetingFemale: '',
          useAutoDialogs: '',
        },
        model: {
          primaryAccountId: '',
          greetingMale: '',
          greetingFemale: '',
          useAutoDialogs: false,
          whatToUseInTargetSettings: 'one',
          targetAuditoryOwnFrom: '',
          targetAuditoryOwnTo: '',
          targetAuditoryAutoCollect: '',
          targetAuditoryRecommendFriends: '',
          useAutoReposting: false,
          fillInAccounts: false,
        },
        selected: '',
        selectOptionsFriend: [5, 10, 15, 20, 30, 40, 50],
        selectOptionsAuto: ["3 (по умолчанию)", 5, 10, 15, 20, 30, 40, 50, 60, 3040, 20, 30, 120, 230],
        greetingTabData: {
          female: {
            id: 0,
            sex: 'Female',
            key: 'for-female',
            title: 'Женское',
            placeholder: 'Женское приветствие',
            model: ''
          },
          male: {
            id: 1,
            sex: 'Male',
            title: 'Мужское',
            placeholder: 'Мужское привествие',
            model: ''
          },
        },
        isActive: false,
        check: false,
        needFill: false,
      }
    },
    computed: {
      primaryAccountId() {
        return this.model.primaryAccountId
      },
      useYourAuditory() {
        return this.model.whatToUseInTargetSettings === 'one';
      },
      useCollectedAuditory() {
        return this.model.whatToUseInTargetSettings === 'two';
      },
      useRecommendMailing() {
        return this.model.whatToUseInTargetSettings === 'three';
      },
      useAutoReposting() {
        return this.model.useAutoReposting
      },
      fromToValidation() {
        return this.model.targetAuditoryOwnFrom.length && this.model.targetAuditoryOwnTo.length
      },
      counter() {
        return 1
      },
    },
    methods: {
      ...mapActions(['showModal', 'openInstructionsModal']),
      saveSelectVal(data) {
        if (this.useYourAuditory) {
          this.dataForBackend.targetAuditoryOwnFrom = data[0];
          this.dataForBackend.targetAuditoryOwnTo = data[1];
        }
        if (this.useCollectedAuditory) {
          this.dataForBackend.targetAuditoryAutoCollect = data;
        }
        if (this.useRecommendMailing) {
          this.dataForBackend.targetAuditoryRecommendFriends = data;
        }
        if (this.primaryAccountId) {
          this.dataForBackend.primaryAccountId = data;
        }
      },
      selection(option) {
        this.selected = String(this.selected = option);
      },
      saveGreeting(data) {
        this.dataForTable.greeting = data;
      },
    }
  }
</script>

<style lang="scss">
	.autopilot-form {
		width: 100%;
		color: #fff;
		.v-block {
			margin-bottom: 30px;
			.v-control-checkbox {
				margin-bottom: 10px;
			}
		}
		@media screen and (min-width: 1024px) {
			width: 40vw;
			/*max-width: calc(90vw - 300px);*/
		}
	}

	.content-ttl {
		margin-bottom: 24px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		&__link {
			display: flex;
			color: #fff;
			align-items: center;
			font-size: 18px;
			text-transform: uppercase;
			/*color: #fff;*/
			&:hover {
				color: #FFC107;
			}
		}
		&__icon {
			margin-right: 16px;
		}
	}

	.block {
		&-account {
			.input-label {
				margin-bottom: 16px;
			}
			.v-instructions {
				margin-bottom: 16px;
			}
		}
		&-target {
			.input-group {
				margin-bottom: 20px;
				.input-wrap {
					margin-bottom: 20px;
					&:last-child {
						margin-right: 0;
					}
				}
				@media screen and (min-width: 448px) {
					display: flex;
					justify-content: space-between;
					.input-wrap {
						width: 47%;
					}
				}
			}
		}
		&-dialogs {

		}
	}

	.v-box {
		margin-bottom: 16px;
	}

	.block-dropdown {
		margin-top: 10px;
		margin-bottom: 10px;
		&__title {
			margin-bottom: 16px;
			font-size: 16px;
		}
		&__row {
			margin-bottom: 20px;
		}
	}

	.tab-add-account {
		max-width: 320px;
		margin-bottom: 20px;
	}

	@media screen and (min-width: 812px) {
		.autopilot-form {
			color: #000;
		}
		.content-ttl__link {
			color: #000;
			font-size: 24px;
		}
	}


</style>